#home {
    display: grid;
    gap: 2rem;

    @media screen and (min-width: $layout-breakpoint-small){
    grid-template-columns: repeat(2,1fr);
    };


    @media screen and (min-width: $layout-breakpoint-medium){
    grid-template-columns: repeat(4,1fr);
    grid-template-rows: repeat(2.1fr);
    gap: 1rem;
    };


    div {
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        &:hover{
            img{
                transition: all 0.3s ease;
                scale: 1.1;
            }
        }

        &:nth-child(1) {
            background-color: rgb(232, 232, 246);

            @media screen and (min-width: $layout-breakpoint-medium){
                grid-column-start: 1;
                grid-column-end: 3;
                grid-row-start: 1;
                grid-row-end: 3;
            }
        }

        &:nth-child(2) {
            background-color: #ede47b;

            @media screen and (min-width: $layout-breakpoint-medium){
                img{
                    width: 20rem;
                    height: 20rem;
                    object-fit: cover;
                }
            }
        }


        &:nth-child(3) {
            background-color: #d69d8c;
        }


        &:nth-child(4) {
            background-color: #b0bace;

            @media screen and (min-width: 
            $layout-breakpoint-medium){
                grid-row-start: 2;
                grid-row-end: 3;
                grid-column-start: 3;
                grid-column-end: 5;


                img{
                    height: 18rem;
                    width: 18rem;
                    object-fit: cover;
                }
            }
        }

        a {
            position: absolute;
            text-decoration: none;
            color: $black;
            text-transform: uppercase;
            bottom: 1rem;
            left: 2rem;
            font-size: 1.2rem;
            font-weight: 500;

            &:hover {
                text-decoration: underline;
            }

        }

    }


}