.scrollToTopBtn {
    position: fixed;
    bottom: 4rem;
    right: 2rem;
    background-color: $black;
    width: 2rem;
    height: 2rem;
    border-radius: 50%;
    z-index:10;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 1;

    svg {
        color: $white;
    }

}