.popular_container{
    display: flex;
    align-items: center;
    justify-content: space-between;


    .nextAndPrev{
        display: flex;
        align-items: center;
        gap: 2rem;


        div{
            cursor: pointer;
            font-weight: 500;
            text-transform: uppercase;
        }
    }
}


.product_container{
    display: grid;
    place-items: center;
    gap: 2rem;
    margin-top: 5rem;

    @media screen and (min-width: $layout-breakpoint-small){
        grid-template-columns: repeat(2,1fr);
    }
    @media screen and (min-width: $layout-breakpoint-medium){
        grid-template-columns: repeat(3,1fr);
    }


    .product{
        padding: 2rem 1rem;
        position: relative;
        
        &:nth-child(1){
            background-color: #d7ccc1;
        }
        &:nth-child(2){
            background-color: #ededee;
        }
        &:nth-child(3){
            background-color: #eedfce;
        }
        &:nth-child(4){
            background-color: #f3eee5;
        }
        &:nth-child(5){
            background-color: #ded2c2;
        }
        &:nth-child(6){
            background-color: #eee8e9;
        }
        &:nth-child(7){
            background-color: #e3b481;
        }
        &:nth-child(8){
            background-color: #dbdfd7;
        }


        .product_img{
            height: 10rem;
            width: 15rem;
            object-fit: cover;
            margin-bottom: 2rem;
        }


        .nameAndPrice{
            text-align: center;

            .name {
                font-size: 0.95rem;
                font-weight: 500;
                font-family: Verdana, Geneva, Tahoma, sans-serif;
            }

            .price{
                margin-top: 0.5rem;
                font-size: 1rem;
                font-weight: 500;
                font-family:Verdana, Geneva, Tahoma, sans-serif ;
            }
            
        }

        
            &:hover {
                .btns{
                    height: 100%;
                    opacity: 1;
                }
        }

        

        .btns{
            position: absolute;
            top: 0;
            left: 0;
            display: flex;
            align-items: center;
            justify-content: center;
            width: 100%;
            height: 0;
            background-color: rgba($color: $white, $alpha: 0.75);
            opacity: 0;
            transition: all 0.5s ease;

            button {
                background-color: $white;
                outline: none;
                border: none;
                padding: 0.45rem;
                border-right: 1px solid $black;
                font-family: "Poppins";
                text-transform: uppercase;
                font-size: 0.75rem;
                font-weight: 500;
                cursor: pointer;
            }

            .heart{
                padding: 0.35rem;
                cursor: pointer;
                font-size: 1.4rem;
                background-color: $white;
            }
        }
    }
}